<template lang="pug">
  .products.favorites.px-3.light-bg-color(
    :class="{ loading }"
  )
    .row
      //- :class="{'is-loading': filteredProductsQuerying}"
      .col-lg-8.px-3.column--results.offset-lg-2

        //- LISTING
        recycle-scroller(
          class="scroller"
          :items="products"
          :item-size="260"
          :buffer="1000"
          key-field="id"

        )
          template(v-slot:before)
            h2.mt-4.w-100.valign.mb-0
              span.h2
                transition(name="slide-up" mode="out-in")
                  span.mr-1(
                    :key="products.length"
                  ) {{products.length}}
                | babysitters
              b-button-group.ml-auto.mr-2
                b-button.ml-auto.valign(
                  @click="sortBy('rate.amount')"
                  variant="white"
                  size="sm"
                  )
                  i.mdi.is-size-5.mr-1 sort
                  | Sort by price

          template(v-slot="{ item }")
            product-list-item.card.my-3.mx-1(:product="item")

</template>

<script>
  import {mapGetters, mapActions} from "vuex";
  import ProductListItem from "../ui/product-list-item";

  export default {
    name: "Favorites",
    components: {
      ProductListItem
    },
    props: [],
    data() {
      return {
        // FORM
        loading: true
      };
    },
    computed: mapGetters([
      'products',
    ]),
    mounted() {
      this.getFavoritesProducts().then(() => {
        this.loading = false
      })
      this.getFavorites()
    },
    methods: mapActions([
      'getFavoritesProducts',
      'getFavorites',
      'sortBy'
    ]),
  };
</script>


